.premium-page .features .row5{
    margin:7.5% 0;
    padding: 0 10%;
    h1{
        font-size: 30px;
        margin-bottom: 50px;
    }
    .items{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; 
    }
    .item5{
        border:1px solid transparent;
        width: 28%;
        padding:30px;
        border-radius: 10px;
    }
    svg{
        color:#427cff;
        font-size: 50px;
    }
    h6{
        font-size: 20px;
        margin:20px 0;
