            font-size: 25px;
            margin-bottom: 40px;
        }      
        .item5{
            width: 30%;
            padding:25px;
        }
        svg{
            font-size: 45px;
        }
        h6{
            font-size: 16px;
            margin:16px 0;
        }
        p{
            font-size: 15px;
        }
