}

@media(max-width:991px){
    .premium-page .features .row5{
       padding:0 5%;    
        .item5{
            width: 32%;
        }
    }
}

@media(max-width:767px){
    .premium-page .features .row5{
       padding:0 7.5%;
       margin:30px 0; 
        h1{
           font-size: 20px;
           margin-bottom: 5px;
        }   
        .item5{
            width: 100%;
            margin:5px 0;
            padding:20px;
        }
        svg{
            font-size: 40px;
        }
        h6{
            margin:15px 0;
        }
    }
}

//======================================
.premium-page .features .row6{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; 
    margin: 5% 0;
    padding:0 15%;
    .images{
        width: 42.5%;
        position: relative;
        &:hover{
            .top-img{
                border-radius: 15%;
            }
            .icon{
                background: #427cff;
                color:#fff;
            }
        }
    }
    .info{
        width: 37.5%;
    }
    h1{
        font-size: 30px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
    .bg-img{
        content: url('../../../../assets/Premium/bg4.png');
        width: 100%;
    }
    .top-img{
        transition: 0.3s ease-in-out;
        content: url('../../../../assets/Premium/img4.png');
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        width: 90%;
        border-radius:5% 15% 5% 5%;
    }
    .icon{
        transition: 0.2s ease-in-out;
        width: 135px;
        height: 135px;
        background: #fff;
        box-shadow: rgba(60, 64, 67, 0.1) 0px 1px 2px 0px, rgba(60, 64, 67, 0.075) 0px 2px 6px 2px;
        font-size: 50px;
        color:#427cff;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top:40%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media(max-width:1401px){
    .premium-page .features .row6{
        margin: 6.25% 0;
        padding:0 10%;
        .images{
            width: 42.5%;
        }
        .info{
            width: 40%;
        }
        h1{
            font-size: 25px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
            line-height: 1.5;
        }
        .icon{
            width: 110px;
            height: 110px;
            font-size: 45px;
            top:35%;
        }
    }
}

@media(max-width:767px){
    .premium-page .features .row6{
        margin: 20px 0;
        padding:0 7.5%;
        .images{
            width: 350px;
            max-width: 100%;
            margin:20px auto;
        }
        .info{
            width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
        }
        .icon{
            width: 90px;
            height: 90px;
            font-size: 40px;
            box-shadow: none;
            border:1px solid #f8f8f8;
        }
    }
    
}


.premium-page .features .row5{
    .item5{
        
        box-sizing: border-box;
        position: relative;
        transition: 0.4s ease-in-out;
        &:hover{
            transform: scale(1.03);
        }
        &::before,
        &::after {
            content: '';
            box-sizing: border-box;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 5px;
            height: 100%;
            width: 100%;
            transform-origin: center;
        }
        &::before {
            border-top: 2px solid #427cff;
            border-bottom: 2px solid #427cff;
            transform: scale3d(0,1,1);
          }
        
          &::after {
            border-left:2px solid #427cff;
            border-right: 2px solid #427cff;
            transform: scale3d(1,0,1);
          }
        
          &:hover::before,
          &:hover::after {
            transform: scale3d(1,1,1);
            transition: transform 400ms;
          }
    }
}

@media(max-width:767px){
    .premium-page .features .row5{
        .item5{
            &:hover{
                transform: scale(1);
            }
            &::before,
            &::after {
                border:0;
            }
        }
    }
}