.premium-page{
    overflow: hidden;
}

.premium-page .access .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:5% 0;
    padding:0 10%;
    .col1{
        width: 40%;
    }
    .images{
        width: 40%;
        position: relative;
        &:hover{
            .top-img{
                border-radius: 10%;
            }
        }
    }
    h1{
        font-size: 33px;
        margin-bottom: 25px;
    }
    .item1{
        display: flex;
        align-items: center;
        margin:25px 0;;
    }
    .icon{
        color:#427cff;
        font-size: 40px;
        margin-right: 25px;
        svg{
            margin-bottom: -5px;
        }
    }
    p{
        flex: 1;
        font-size: 18px;
    }
    .bg-img{
        content: url('../../../../assets/Premium/bg1.png');
        width: 100%;
    }
    .top-img{
        transition: 0.35s ease-in-out;
        content: url('../../../../assets/Premium/img1.png');
        width: 85%;
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
    }
    .logo{
        content: url('../../../../assets/HowItWorks/blue-logo.png');
        width: 17.5%;
        position: absolute;
        left:0;
        right:0;
        margin-left: auto;
        margin-right: auto;
        bottom:-12.5%;
    }
}

@media(max-width:1401px){
    .premium-page .access .row1{
        margin:7.5% 0;
        .col1{
            width: 45%;
        }
        .images{
            width: 40%;
        }
        h1{
            font-size: 27.5px;
            margin-bottom: 20px;
        }
        .item1{
            margin:20px 0;;
        }
        .icon{
            font-size: 35px;
            margin-right: 20px;
            svg{
                margin-bottom: -5px;
            }
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .premium-page .access .row1{
       padding:0 5%;
        .col1{
            width: 47.5%;
        }
        .images{
            width: 42.5%;
        }        
    }
}
@media(max-width:767px){
    .premium-page .access .row1{
       padding:0 7.5%;
       margin:30px 0;
       h1{
           font-size: 22px;
       }
        .col1{
            width: 100%;
            margin:20px 0;
        }
        .images{
            width: 350px;
            max-width: 92.5%;
            margin:20px auto;
        }        
    }
}

//======================================
.premium-page .access .row2{
    margin:10% 0;
    position: relative;
    .bg{
        position: absolute;
        right:-10%;
        top:80px;
        width: 40%;
        content: url('../../../../assets/Premium/bg.png');
    }
    h1{
        margin:0 10%;
        font-size: 35px;
    }
    .p{
        font-size: 20px;
        margin:15px 10% 35px;
    }
    .items{
        display: flex;
        flex-wrap: wrap;
        position: relative;
        padding:0 10%;
    }
    .item2{
        width: 25%;
        margin-right: 5%;
        background-color: #fff;
        padding:25px 25px 60px;
        border:1px solid #eee;
        position: relative;
        z-index: 2;
        border-radius: 10px;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: translateY(-10px);
            .line{
                width: 90%;
            }
        }
    }
    .icon{
        color:#427cff;
        font-size: 50px;
    }
    h6{
        font-size: 18px;
        margin:17.5px 0;
    }
    .item2 p{
        font-size: 18px;
    }
    .line{
        transition: 0.35s ease-in-out;
        width: 70%;
        height: 12px;
        border-radius: 8px;
        background: #427cff;
        position: absolute;
        bottom:-6px;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
    }
}

@media(max-width:1401px){
    .premium-page .access .row2{
        .bg{
            position: absolute;
            right:-10%;
            top:75px;
            width: 40%;
        }
        h1{
            font-size: 27.5px;
        }
        .p{
            font-size: 15px;
            margin:15px 10% 30px;
        }
        .item2{
            width: 25%;
            margin-right: 5%;
            padding:20px 20px 55px;
        }
        .icon{
            font-size: 42.5px;
        }
        h6{
            font-size: 15px;
            margin:15px 0;
        }
        .item2 p{
            font-size: 15px;
        }
        .line{
            width: 70%;
            height: 9px;
            border-radius: 6px;
            bottom:-4.5px;
        }
    } 
}

@media(max-width:991px){
        .premium-page .access .row2{
            margin:10% 0;
            .bg{
                position: absolute;
                right:-10%;
                top:75px;
                width: 40%;
            }
            h1{
                margin:0 7.5%;
                font-size: 22.5px;
                text-align: center;
            }
            .p{
                text-align: center;
                margin:15px 7.5% 30px;
            }
            .items{
                justify-content: space-between;
            }
            .item2{
                width: 30%;
                margin:0; 
                padding:20px 20px 55px;
            }
            .icon{
                font-size: 40px;
            }
        } 
}

@media(max-width:800px){
    .premium-page .access .row2{
        .bg{
            right:unset;
            top:50%;
            left:50%;
            transform: translate(-50%, -50%);
            width: 65%;
        }
        .items{
            justify-content: center;
            
        }
        .item2{
            width: 45%;
            margin:10px 2.5%;
        }
    }
}

@media(max-width:525px){
    .premium-page .access .row2{
        .bg{
            width: 95%;
        }
        .item2{
            width: 100%;
            margin:12.5px 0%;
        }
    }
}