.premium-page .security .left-right{
    margin:5% 0;
    padding:0 10% 0 0;
    .col1{
        width: 37.5%;
    }
    h1{
        font-size: 33px;
    }
    p{
        margin:25px 0 40px;
    }
    .other-elements{
        width: 50%;
    }
    .created-img{
        width: 100%;
        position: relative;
    }
    .bg-img{
        content: url('../../../../assets/Premium/bg2.png');
        width: 100%;
    }
    .top-img{
        width: 90%;
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        content: url('../../../../assets/Premium/img2.png');
    }
}

@media(max-width:1401px){
    .premium-page .security .left-right{
        margin:6.5% 0;
        .col1{
            width: 40%;
        }
        h1{
            font-size: 25px;
        }
        p{
            margin:20px 0 35px;
            font-size: 16px;
            line-height: 1.5;
        }
        .other-elements{
            width: 50%;
        }
    }
}

@media(max-width:767px){
    .premium-page .security .left-right{
        margin:30px 0;
        padding:0 7.5%;
        .col1{
            width: 100%;
            margin:20px 0;
        }
        h1{
            font-size: 20px;
        }
        p{
            margin:15px 0 30px;
            font-size: 16px;
            line-height: 1.4;
        }
        .other-elements{
            width: 100%;
            margin:20px 0;
        }
    }
}

//=============================================
.premium-page .security .row4{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:10% 0 5%;
    padding:0 10%;
    .info1{
        width: 42.5%;
    }
    .images{
        width: 42.5%;
        position: relative;
        &:hover{
            .top-img{
                border-radius: 20%;
            }
        }
    }
    h1{
        font-size: 35px;
    }
    .link{
        margin-top: 45px;
    }
    .bg-img{
        content: url('../../../../assets/Premium/bg3.png');
        position: absolute;
        width: 50%;
        top:-15%;
        right:-15%;
    }
    .top-img{
        transition: 0.35s ease-in-out;
        width: 100%;
        content: url('../../../../assets/Premium/img3.png');
        border-radius: 7.5%;
        position: relative;
        z-index: 2;
    }
}

@media(max-width:1401px){
    .premium-page .security .row4{
        .info1{
            width: 45%;
        }
        .images{
            width: 40%;
        }
        h1{
            font-size: 27.5px;
        }
        .link{
            margin-top: 40px;
        }
    }
}

@media(max-width:767px){
    .premium-page .security .row4{
        padding:0 7.5%;
        flex-wrap: wrap;
        .info1{
            width: 100%;
            margin:20px 0 20%;
        }
        .images{
            width: 85%;
            margin:20px auto;
        }
        h1{
            font-size: 22.5px;
        }
        .link{
            margin-top: 30px;
        }
    }
}